@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{font-family:'Roboto', sans-serif;background-color:#223343}header{padding:50px 15px 0px}header h1{color:#FFF;font-size:48px;font-weight:500;text-align:center}main{width:100%;max-width:960px;margin:0 auto}section{padding:15px}.results{display:flex;flex-wrap:wrap}.results .result{display:flex;flex-direction:column;cursor:pointer;position:relative;min-width:475px;height:250px;margin:15px auto;overflow:hidden;border-radius:10px;-webkit-transition:all 0.4s;transition:all 0.4s;box-shadow:0px 0px 120px -25px rgba(0,0,0,0.5)}.results .result:hover{-webkit-transform:scale(1.02);transform:scale(1.02);box-shadow:0px 0px 80px -25px rgba(0,0,0,0.5);-webkit-transition:all 0.4s;transition:all 0.4s}.results .result .info-section{position:relative;width:100%;height:100%;background-image:-webkit-gradient(linear, left top, right top, color-stop(50%, #fff), color-stop(130%, transparent));background-image:linear-gradient(to right, #fff 50%, transparent 130%);z-index:2;border-radius:10px}.results .result .info-section .card-header{position:relative;padding:15px;height:40%}.results .result .info-section .card-header h1{color:black;font-weight:400;font-size:20px}.results .result .info-section .card-header h4{color:#555;font-weight:400}.results .result .info-section .card-header .type{display:inline-block;margin-top:15px;color:#555;padding:5px;border-radius:5px;border:1px solid rgba(0,0,0,0.05)}.results .result .info-section .card-header .image-poster{position:relative;float:left;margin-right:20px;height:120px;box-shadow:0 0 20px -10px rgba(0,0,0,0.5)}.results .result .info-section .movie-spacer{padding:25px;height:50%}.results .result .info-section .movie-social{height:10%;padding-left:10px;padding-bottom:10px}.results .result .info-section .movie-social ul{list-style:none;padding:0}.results .result .info-section .movie-social ul li{display:inline-block;color:rgba(0,0,0,0.3);-webkit-transition:color 0.3s;transition:color 0.3s;-webkit-transition-delay:0.15s;transition-delay:0.15s;margin:0 10px}.results .result .info-section .movie-social ul li:hover{-webkit-transition:color 0.3s;transition:color 0.3s;color:rgba(0,0,0,0.7)}.results .result .info-section .movie-social ul li .icon{cursor:pointer}.results .result .blur-back img{position:absolute;top:0;z-index:1;border-radius:5px;height:100%;right:0;background-size:cover;border-radius:11px;width:11rem}@media screen and (min-width: 768px){.card-header{width:65%}.info-section{background:-webkit-gradient(linear, left top, right top, color-stop(25%, #e5e6e6), color-stop(75%, transparent));background:linear-gradient(to right, #e5e6e6 25%, transparent 75%)}.blur-back{width:80%;background-position:-100% 10% !important}}@media screen and (max-width: 768px){.result{width:70%;margin:70px auto;min-height:350px;height:auto}.blur-back{width:100%;background-position:50% 50% !important}.card-header{width:100%;margin-top:85px}.info-section{background:-webkit-gradient(linear, left bottom, left top, color-stop(50%, #e5e6e6), to(transparent));background:linear-gradient(to top, #e5e6e6 50%, transparent 100%);display:inline-grid}}.logo{width:100px}.header-container{text-align:center}

.searchbox-wrap{display:flex}.search-btn{padding:10px 0px 0px 10px;color:lightslategrey;-webkit-transition:-webkit-transform .5s ease;transition:-webkit-transform .5s ease;transition:transform .5s ease;transition:transform .5s ease, -webkit-transform .5s ease}.search-btn:hover{color:#fff;cursor:pointer;-webkit-transform:scale(1.2);transform:scale(1.2)}.searchbox{flex-grow:4;width:100%;padding:15px;border:none;outline:none;background:none;background-color:#EEE;border-radius:8px;color:#53565A;font-size:20px;font-weight:300;-webkit-transition:0.4s ease-out;transition:0.4s ease-out}.searchbox:focus{box-shadow:0px 0px 8px 3px #4484C4}

h1,h2,h3,h4,h5{font-family:"Montserrat",sans-serif;color:#e7e7e7;margin:0px}h1{font-size:28px;font-weight:400}h5{font-size:12px;font-weight:400;color:#e7e7e7}.popup{position:fixed;top:0;left:0;width:100vw;height:100vh;background:#223343;z-index:2;overflow-y:scroll}.modal-container{width:310px;height:550px;margin:25px auto 0 auto;background-color:white;box-shadow:5px 5px 115px -14px whitesmoke;border-radius:4px}.movie{background:-webkit-gradient(linear, left top, left bottom, color-stop(38%, rgba(255,255,255,0)), color-stop(39%, rgba(255,255,255,0.06)), color-stop(23%, rgba(156,156,156,0.88)), color-stop(55%, #000));background:linear-gradient(to bottom, rgba(255,255,255,0) 38%, rgba(255,255,255,0.06) 39%, rgba(156,156,156,0.88) 23%, #000 55%);filter:progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#1e1b26',GradientType=0 );position:absolute;background-size:contain;background-size:cover;z-index:1 !important;width:310px;height:580px;display:block;border-radius:4px}.text-movie-cont{padding:0px 12px;text-align:justify}.closeBtn{position:absolute;right:12px;top:6px;z-index:999}.closeBtn .times-circle{font-size:40px;color:#fe4141;-webkit-filter:drop-shadow(4px 4px 10px rgba(0,0,0,0.5));filter:drop-shadow(4px 4px 10px rgba(0,0,0,0.5));-webkit-transition:-webkit-transform .3s;transition:-webkit-transform .3s;transition:transform .3s;transition:transform .3s, -webkit-transform .3s}.closeBtn .times-circle:hover{-webkit-transform:scale(1.2);transform:scale(1.2);cursor:pointer}.image-poster-modal{width:100%;height:330px;background-position:center;background-repeat:no-repeat;background-size:cover;z-index:111 !important;border-top-left-radius:4px;border-top-right-radius:4px;-webkit-mask-image:-webkit-gradient(linear, left top, left bottom, color-stop(0, #000), color-stop(0.35, #000), color-stop(0.5, #000), color-stop(0.65, #000), color-stop(0.85, rgba(0,0,0,0.6)), color-stop(1, rgba(0,0,0,0)));mask-image:-webkit-gradient(linear, left top, left bottom, color-stop(0, #000), color-stop(0.35, #000), color-stop(0.5, #000), color-stop(0.65, #000), color-stop(0.85, rgba(0,0,0,0.6)), color-stop(1, rgba(0,0,0,0)));position:relative}.movie-gen,.movie-icons{margin:0px;padding:0px}.movie-gen li,.movie-icons li{font-family:"Open Sans",sans-serif;font-size:12px;color:#9b9b9b;width:auto;display:block;float:left;margin-right:6px;font-weight:600}.movie-icons{float:right}.movie-icons li{color:#fe4141}.movie-icons li:last-child{margin-right:0px}.movie-icons li i{font-size:14px;margin-right:4px;position:relative;float:left;line-height:1}.movie-description{font-family:"Open Sans",sans-serif;font-size:12px;font-weight:400;color:#9b9b9b;text-align:justify;line-height:1.4}.thegrid{margin:0 auto}.elements-distance,.movie-description,.rating{margin:0px}.mr-grid{width:100%}.mr-grid:before,.mr-grid:after{content:"";display:table}.mr-grid:after{clear:both}.mr-grid{*zoom:1}.col1,.col2,.col3,.col3rest,.col4,.col4rest,.col5,.col5rest,.col6,.col6rest{margin:0% 0.5% 0.5% 0.5%;padding:1%;float:left;display:block}.col1{width:98%}.col2{width:47%}.col3{width:30.3333333333%}.col4{width:22%}.col5{width:17%}.col6{width:13.6666666667%}.col3rest{width:63.6666666667%}.col4rest{width:72%}.col5rest{width:77%}.col6rest{width:80.3333333333%}.summary-row{margin-top:12px}.rating{font-family:"Open Sans",sans-serif;font-size:12px;font-weight:300;color:#e7e7e7;font-style:italic}

.loader-container{background:linear-gradient(135deg, #EFEFFA 0%, #355C7D 71%);height:100vh;opacity:1;position:fixed;top:0;width:100vw;z-index:100;-webkit-transition:opacity 5s;transition:opacity 5s}.loader--hide{opacity:0}.loader{position:absolute;top:calc(50% - 32px);left:calc(50% - 32px);width:64px;height:64px;border-radius:50%;-webkit-perspective:800px;perspective:800px}.inner{position:absolute;box-sizing:border-box;width:100%;height:100%;border-radius:50%}.inner.one{left:0%;top:0%;-webkit-animation:rotate-one 1s linear infinite;animation:rotate-one 1s linear infinite;border-bottom:3px solid #EFEFFA}.inner.two{right:0%;top:0%;-webkit-animation:rotate-two 1s linear infinite;animation:rotate-two 1s linear infinite;border-right:3px solid #EFEFFA}.inner.three{right:0%;bottom:0%;-webkit-animation:rotate-three 1s linear infinite;animation:rotate-three 1s linear infinite;border-top:3px solid #EFEFFA}@-webkit-keyframes rotate-one{0%{-webkit-transform:rotateX(35deg) rotateY(-45deg) rotateZ(0deg);transform:rotateX(35deg) rotateY(-45deg) rotateZ(0deg)}100%{-webkit-transform:rotateX(35deg) rotateY(-45deg) rotateZ(360deg);transform:rotateX(35deg) rotateY(-45deg) rotateZ(360deg)}}@keyframes rotate-one{0%{-webkit-transform:rotateX(35deg) rotateY(-45deg) rotateZ(0deg);transform:rotateX(35deg) rotateY(-45deg) rotateZ(0deg)}100%{-webkit-transform:rotateX(35deg) rotateY(-45deg) rotateZ(360deg);transform:rotateX(35deg) rotateY(-45deg) rotateZ(360deg)}}@-webkit-keyframes rotate-two{0%{-webkit-transform:rotateX(50deg) rotateY(10deg) rotateZ(0deg);transform:rotateX(50deg) rotateY(10deg) rotateZ(0deg)}100%{-webkit-transform:rotateX(50deg) rotateY(10deg) rotateZ(360deg);transform:rotateX(50deg) rotateY(10deg) rotateZ(360deg)}}@keyframes rotate-two{0%{-webkit-transform:rotateX(50deg) rotateY(10deg) rotateZ(0deg);transform:rotateX(50deg) rotateY(10deg) rotateZ(0deg)}100%{-webkit-transform:rotateX(50deg) rotateY(10deg) rotateZ(360deg);transform:rotateX(50deg) rotateY(10deg) rotateZ(360deg)}}@-webkit-keyframes rotate-three{0%{-webkit-transform:rotateX(35deg) rotateY(55deg) rotateZ(0deg);transform:rotateX(35deg) rotateY(55deg) rotateZ(0deg)}100%{-webkit-transform:rotateX(35deg) rotateY(55deg) rotateZ(360deg);transform:rotateX(35deg) rotateY(55deg) rotateZ(360deg)}}@keyframes rotate-three{0%{-webkit-transform:rotateX(35deg) rotateY(55deg) rotateZ(0deg);transform:rotateX(35deg) rotateY(55deg) rotateZ(0deg)}100%{-webkit-transform:rotateX(35deg) rotateY(55deg) rotateZ(360deg);transform:rotateX(35deg) rotateY(55deg) rotateZ(360deg)}}

